.page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: #f1f1f1;
  color: #111;
}

.page main {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.topbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 26px 48px;
  background: #f1f1f1;
  position: sticky;
  top: 0;
  z-index: 10;
}

.brand {
  display: flex;
  align-items: center;
  gap: 12px;
  text-decoration: none;
  color: inherit;
}

.dot {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: #d3bf0f;
}

.identity {
  display: flex;
  align-items: baseline;
  gap: 10px;
  font-weight: 600;
}

.name {
  font-size: 20px;
  letter-spacing: 0.2px;
}

.title {
  font-size: 15px;
  font-weight: 500;
  color: #555;
}

.nav-links {
  display: flex;
  align-items: center;
  gap: 14px;
  font-size: 17px;
  color: #222;
}

.nav-links a {
  color: inherit;
  text-decoration: none;
  font-weight: 500;
  padding: 6px 4px;
}

.nav-links a:hover {
  text-decoration: none;
}

.divider {
  color: #888;
}

.nav-toggle {
  display: none;
  flex-direction: column;
  gap: 5px;
  width: 42px;
  height: 42px;
  align-items: center;
  justify-content: center;
  background: #ffffff;
  border: 1px solid #dcdcdc;
  border-radius: 10px;
  box-shadow: 0 10px 18px rgba(0, 0, 0, 0.08);
  cursor: pointer;
  padding: 8px;
}

.nav-toggle span {
  width: 18px;
  height: 2px;
  background: #111;
  transition: transform 0.2s ease, opacity 0.2s ease;
}

.nav-toggle.open span:nth-child(1) {
  transform: translateY(7px) rotate(45deg);
}

.nav-toggle.open span:nth-child(2) {
  opacity: 0;
}

.nav-toggle.open span:nth-child(3) {
  transform: translateY(-7px) rotate(-45deg);
}

.hero {
  margin-top: 6px;
  display: grid;
  grid-template-columns: minmax(320px, 540px) 1fr;
  align-items: center;
  gap: 68px;
  padding: 70px 200px 70px;
}

.photo-wrap {
  width: 520px;
  max-width: 100%;
  aspect-ratio: 1 / 1;
  border-radius: 50%;
  overflow: hidden;
  background: linear-gradient(135deg, #c5c5c5 0%, #9d9d9d 100%);
  box-shadow: 0 16px 26px rgba(0, 0, 0, 0.08);
  display: flex;
  align-items: center;
  justify-content: center;
}

.photo-wrap img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: grayscale(100%);
}

.intro h1 {
  margin: 0 0 10px;
  font-size: 86px;
  font-weight: 800;
  letter-spacing: -1px;
  color: #0d0d0d;
}

.intro h2 {
  margin: 0 0 12px;
  font-size: 24px;
  font-weight: 700;
}

.intro p {
  max-width: 560px;
  margin: 0 0 36px;
  font-size: 17px;
  line-height: 1.55;
  color: #3b3b3b;
}

.cta-row {
  display: flex;
  flex-wrap: wrap;
  gap: 22px;
}

.cta {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 2px solid #111;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  font-weight: 800;
  color: #222;
  text-decoration: none;
  box-shadow: 0 12px 22px rgba(0, 0, 0, 0.12);
  transition: transform 0.2s ease, box-shadow 0.2s ease, background-color 0.2s ease,
    color 0.2s ease;
}

.cta:hover {
  transform: translateY(-4px);
  box-shadow: 0 16px 26px rgba(0, 0, 0, 0.16);
  background: #ffffff;
  color: #222;
  border-color: #111;
}

.cta.resume {
  background: #4C6EF5;
}
.cta.resume:hover {
  background: #e9dcdc;
}
.cta.projects {
  background: #FF6B6B;
}
.cta.projects:hover {
  background: #e9dcdc;
}

.cta.contact {
  background: #63E6BE;
}
.cta.contact:hover {
  background: #e9dcdc;
}

.section {
  padding: 40px 48px 60px;
}

.section-head h2 {
  margin: 0 0 12px;
  font-size: 32px;
  font-weight: 800;
}

.section-head p {
  margin: 0;
  font-size: 17px;
  color: #3b3b3b;
}

.resume-page {
  background: #f7f7f7;
  min-height: 100vh;
  padding: 12px 0 80px;
}

.resume-hero {
  padding: 10px 48px 30px;
  max-width: 1140px;
  margin: 0 auto;
  width: 100%;
  margin-bottom: 36px;
}

.resume-hero h1 {
  margin: 0;
  font-size: 54px;
  font-weight: 800;
  color: #111;
}

.resume-content {
  display: flex;
  flex-direction: column;
  gap: 80px;
  padding: 0 48px;
  max-width: 1140px;
  margin: 0 auto;
  width: 100%;
}

.resume-block {
  display: grid;
  grid-template-columns: minmax(200px, 260px) 1fr;
  gap: 48px;
  align-items: start;
  padding-bottom: 32px;
  border-bottom: 1px solid #dcdcdc;
}

.resume-side-title {
  font-size: 26px;
  font-weight: 800;
  line-height: 1.2;
}

.resume-list {
  display: flex;
  flex-direction: column;
  gap: 34px;
}

.resume-entry {
  display: grid;
  grid-template-columns: 160px 1fr;
  gap: 28px;
}

.resume-time {
  font-size: 15px;
  font-weight: 600;
  color: #444;
}

.resume-details h3 {
  margin: 0 0 8px;
  font-size: 19px;
  font-weight: 800;
}

.resume-details p {
  margin: 0;
  font-size: 16px;
  color: #2f2f2f;
  line-height: 1.6;
}

.resume-details ul {
  margin: 6px 0 0 18px;
  padding: 0;
  color: #2f2f2f;
  line-height: 1.5;
}

.resume-details .skills-list {
  margin-left: 0;
}

.skills-list {
  list-style: none;
  margin: 10px 0 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 10px 14px;
}

.skill-icon {
  display: flex;
  align-items: center;
  gap: 10px;
}

.skill-icon-circle {
  width: 36px;
  height: 36px;
  border-radius: 10px;
  background: #f5f5f5;
  border: 1px solid #e2e2e2;
  display: grid;
  place-items: center;
}

.skill-icon svg {
  width: 22px;
  height: 22px;
  fill: currentColor;
}

.resume-block:last-of-type {
  border-bottom: 1px solid #dcdcdc;
  padding-bottom: 32px;
}

.projects-page {
  background: #f7f7f7;
  padding: 12px 0 80px;
  min-height: 100vh;
}

.projects-hero {
  padding: 10px 48px 30px;
  margin-bottom: 40px;
}

.projects-hero h1 {
  margin: 0;
  font-size: 54px;
  font-weight: 800;
  color: #111;
}

.projects-list {
  display: flex;
  flex-direction: column;
  gap: 110px;
  padding: 0 48px;
  max-width: 1160px;
  margin: 0 auto;
}

.project-row {
  display: grid;
  grid-template-columns: minmax(280px, 520px) minmax(320px, 520px);
  gap: 70px;
  align-items: center;
  justify-content: center;
  padding: 28px;
  border: 1px solid #e7e7e7;
  border-radius: 16px;
  background: #ffffff;
  box-shadow: 0 12px 28px rgba(0, 0, 0, 0.08);
  transition: transform 0.25s ease, box-shadow 0.25s ease, border-color 0.25s ease;
}

.project-link {
  text-decoration: none;
  color: inherit;
  display: block;
  border-radius: 12px;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.project-link:hover {
  text-decoration: none;
}

.project-link[aria-disabled='true'] {
  cursor: default;
}

.project-link:hover .project-row,
.project-link:focus-visible .project-row {
  box-shadow: 0 18px 36px rgba(0, 0, 0, 0.14);
  transform: translateY(-6px);
  border-color: #d0d0d0;
}

.project-link:focus-visible {
  outline: 2px solid #0a66c2;
  outline-offset: 6px;
  border-radius: 14px;
}

.project-image {
  overflow: hidden;
  border-radius: 12px;
}

.project-image img {
  transition: transform 0.35s ease;
}

.project-link:hover .project-image img,
.project-link:focus-visible .project-image img {
  transform: scale(1.05);
}

.project-text h2 {
  margin: 0 0 12px;
  font-size: 26px;
  font-weight: 800;
}

.project-text p {
  margin: 0;
  font-size: 17px;
  line-height: 1.6;
  color: #2e2e2e;
}

.project-image img {
  width: 100%;
  height: auto;
  display: block;
  border-radius: 6px;
  box-shadow: 0 16px 32px rgba(0, 0, 0, 0.14);
}

.resume-section {
  background: #f7f7f7;
}

.resume-actions {
  display: flex;
  align-items: center;
  gap: 18px;
  margin-top: 22px;
  flex-wrap: wrap;
}

.download-btn {
  padding: 14px 22px;
  background: #111;
  color: #fdfdfd;
  text-decoration: none;
  border-radius: 30px;
  font-weight: 700;
  letter-spacing: 0.2px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.18);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.download-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 14px 24px rgba(0, 0, 0, 0.2);
}

.download-btn.small {
  padding: 12px 20px;
  font-size: 15px;
  box-shadow: 0 8px 18px rgba(0, 0, 0, 0.14);
}

.resume-download {
  display: flex;
  justify-content: center;
}

.resume-note {
  font-size: 14px;
  color: #595959;
}

.contact-section {
  background: #f7f7f7;
}

.footer {
  display: flex;
  flex-direction: column;
  gap: 12px;
  align-items: center;
  justify-content: center;
  padding: 26px 16px 32px;
  background: #e8e8e8;
  border-top: 1px solid #dedede;
  margin-top: auto;
}

.socials {
  display: flex;
  gap: 18px;
  flex-wrap: wrap;
  justify-content: center;
}

.socials a {
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 46px;
  height: 46px;
  border-radius: 14px;
  background: #f6f6f6;
  border: none;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease, box-shadow 0.2s ease, filter 0.2s ease;
}

.socials a:hover {
  transform: translateY(-3px);
  box-shadow: 0 16px 32px rgba(0, 0, 0, 0.16);
  filter: brightness(1.02);
}

.socials svg {
  width: 22px;
  height: 22px;
  fill: currentColor;
  stroke: currentColor;
  stroke-width: 0;
}

.footer-note {
  font-size: 14px;
  color: #4a4a4a;
}

.socials .instagram {
  color: #e4405f;
  background: #ffe6ed;
  border-color: rgba(228, 64, 95, 0.35);
}

.socials .instagram:hover {
  background: #ffd2df;
}

.socials .linkedin {
  color: #f7fbff;
  background: linear-gradient(135deg, #0a66c2, #0d7ae5);
}

.socials .github {
  color: #f6f8fa;
  background: linear-gradient(135deg, #161b22, #24292e);
}

.socials .portfolio {
  color: #f0fdfa;
  background: linear-gradient(135deg, #0d9488, #14b8a6);
}

.socials .email {
  color: #fff7f7;
  background: linear-gradient(135deg, #d94848, #f87171);
  border: 1px solid #d44638;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

@media (max-width: 1060px) {
  .projects-hero {
    text-align: center;
  }

  .projects-list {
    padding-left: 28px;
    padding-right: 28px;
    gap: 70px;
    max-width: 900px;
  }

  .project-row {
    grid-template-columns: 1fr;
    text-align: center;
  }

  .project-text p {
    margin: 0 auto;
    max-width: 560px;
  }

  .resume-hero {
    text-align: center;
  }

  .resume-content {
    padding-left: 28px;
    padding-right: 28px;
    gap: 60px;
  }

  .resume-block {
    grid-template-columns: 1fr;
    text-align: left;
    gap: 32px;
  }

  .resume-entry {
    grid-template-columns: 1fr;
    justify-items: flex-start;
    width: 100%;
  }

  .resume-side-title {
    margin-bottom: 8px;
  }

  .resume-details p,
  .resume-details ul {
    margin-left: 0;
    margin-right: 0;
    max-width: 100%;
  }

  .skills-list {
    grid-template-columns: repeat(auto-fit, minmax(130px, 1fr));
  }

  .resume-details ul {
    margin-left: 18px;
  }

  .intro h1 {
    font-size: 76px;
  }

  .intro h2 {
    font-size: 22px;
  }

  .intro p {
    margin-left: auto;
    margin-right: auto;
  }

  .cta-row {
    justify-content: center;
  }
}

@media (max-width: 860px) {
  .topbar {
    padding: 18px 20px;
    position: sticky;
    top: 0;
    z-index: 20;
  }

  .nav-toggle {
    display: inline-flex;
  }

  .nav-links {
    position: absolute;
    top: 100%;
    right: 20px;
    left: 20px;
    margin-top: 10px;
    background: #ffffff;
    border: 1px solid #e0e0e0;
    border-radius: 12px;
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.12);
    padding: 12px 0;
    flex-direction: column;
    gap: 0;
    opacity: 0;
    pointer-events: none;
    transform: translateY(-6px);
    transition: opacity 0.2s ease, transform 0.2s ease;
  }

  .nav-links.open {
    opacity: 1;
    pointer-events: auto;
    transform: translateY(0);
  }

  .nav-links a {
    width: 100%;
    padding: 12px 18px;
    text-align: left;
  }

  .divider {
    display: none;
  }

  .hero {
    padding: 32px 20px 60px;
  }

  .intro h1 {
    font-size: 62px;
  }

  .intro h2 {
    font-size: 22px;
  }

  .cta {
    width: 130px;
    height: 130px;
    font-size: 18px;
  }
}

@media (max-width: 640px) {
  .topbar {
    flex-direction: row;
    gap: 14px;
  }

  .projects-hero h1 {
    font-size: 46px;
  }

  .resume-hero h1 {
    font-size: 46px;
  }

  .resume-content {
    padding-left: 20px;
    padding-right: 20px;
  }

  .hero {
    padding: 28px 16px 52px;
  }
}

@media (max-width: 720px) {
  .page {
    max-width: 520px;
    margin: 0 auto;
    background: #ffffff;
    box-shadow: 0 20px 50px rgba(0, 0, 0, 0.12);
    border-radius: 18px;
    overflow: hidden;
  }

  .topbar {
    max-width: 520px;
    margin: 0 auto;
    padding: 18px 18px;
    box-shadow: 0 6px 18px rgba(0, 0, 0, 0.05);
  }

  .hero {
    grid-template-columns: 1fr;
    padding: 32px 20px 48px;
    gap: 32px;
    align-items: flex-start;
    justify-items: flex-start;
    max-width: 520px;
    margin: 0 auto;
  }

  .intro {
    width: 100%;
  }

  .intro h1 {
    font-size: 56px;
    margin-bottom: 12px;
  }

  .intro h2 {
    font-size: 20px;
    margin-bottom: 14px;
  }

  .intro p {
    font-size: 16px;
    max-width: 480px;
  }

  .cta-row {
    gap: 14px;
    justify-content: flex-start;
  }

  .cta {
    width: 120px;
    height: 120px;
    font-size: 17px;
  }

  .photo-wrap {
    width: 320px;
    max-width: 100%;
  }
}

.contact-page {
  background: #f7f7f7;
  min-height: 100vh;
  padding: 40px 24px 80px;
}

.contact-inner {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1.1fr 1.4fr;
  gap: 80px;
  align-items: start;
}

.contact-info h1 {
  margin: 0 0 10px;
  font-size: 46px;
  font-weight: 800;
}

.contact-info p {
  margin: 0 0 32px;
  font-size: 17px;
  color: #3a3a3a;
}

.contact-detail + .contact-detail {
  margin-top: 20px;
}

.contact-label {
  font-weight: 800;
  margin-bottom: 4px;
}

.contact-value {
  color: #2d2d2d;
  font-size: 16px;
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.form-row {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 18px;
}

.field {
  display: flex;
  flex-direction: column;
  gap: 6px;
  font-size: 15px;
  color: #202020;
}

.field input,
.field textarea {
  width: 100%;
  padding: 12px 10px;
  border: 1px solid #1a1a1a;
  border-radius: 0;
  background: #fff;
  font-size: 15px;
  font-family: inherit;
  color: #111;
}

.field textarea {
  min-height: 160px;
  resize: vertical;
}

.submit-btn {
  margin-top: 10px;
  align-self: flex-start;
  padding: 14px 26px;
  min-width: 240px;
  border-radius: 999px;
  border: 1px solid #000000;
  background: #000000;
  color: #ffffff;
  font-weight: 800;
  font-size: 17px;
  cursor: pointer;
  transition: transform 0.15s ease, box-shadow 0.15s ease, filter 0.15s ease;
}

.submit-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.12);
  filter: brightness(1.02);
}

.submit-btn:active {
  transform: translateY(0);
  box-shadow: none;
}

@media (max-width: 960px) {
  .contact-inner {
    grid-template-columns: 1fr;
    gap: 40px;
  }

  .submit-btn {
    width: 100%;
    align-self: stretch;
  }
}
